
    .transaction{
      align-items: center;
      background-color:  #ffffff;
      border-radius: 5px;
      padding: 1rem;
      margin-top: 1rem;
     
    }
    .status {
      padding: 5px;
      border-radius: 5px;
  
      &.Approved {
        color: green;
        background-color: rgba(0, 128, 0, 0.151);
      }
      &.Pending {
        color: goldenrod;
        background-color: rgba(189, 189, 3, 0.103);
      }
    }
  